.dropdown {
  width: 50%;
  height: 4rem;
  min-width: 100px;
  box-shadow: 0px 2px 4px rgb(150, 150, 150);
  border-radius: 5px;
  position: relative;
  display: flex;
  align-items: center;
}
.contaier-text-dropdown {
  width: 100%;
  height: 100%;
  font-size: 1rem;
  display: flex;
  align-items: center;
  padding-left: 1.2rem;
}
.dropButton {
  width: 4.7rem;
  height: 85%;
  border: 0px solid #00b4cc;
  border-left: 2.5px solid #035a7073;
  background: #ffffff;
  color: #ffffff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.options {
  width: 70%;
  height: auto;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  padding: 1rem 4rem 2rem 4rem;
  box-sizing: border-box;
  font-size: 1rem;
  border-top: 2.5px solid #035a7073;
  box-shadow: 0px 2px 4px rgb(150, 150, 150);
  border-radius: 0px 0px 5px 5px;
}
.row-option {
  display: flex;
  width: 100%;
  border-bottom: 2.5px solid #035a7073;
  cursor: pointer;
}
.dropdown-selected {
  width: 50%;
  height: 4rem;
  min-width: 100px;
  box-shadow: 0px 2px 4px rgb(150, 150, 150);
  border-bottom: none;
  border-radius: 5px 5px 0px 0px;
  position: relative;
  display: flex;
  align-items: center;
}
