

.image-top {
    box-shadow: 1px 1px 5px gray;
    border-radius: 5px;
    margin: 0.5em;
}


.image-top-header {
    display: flex;
    justify-content: space-between;
    padding: 0 0.5em;
    align-items: center;
}

.image-top-header-header{
    display: flex;
    align-items: center;
}

.image-top-header-content{
    text-align: left;
}

.image-top-header-content-headline{
    font-weight: bold;
}

.image-top-button{
    margin: 0.5em;
    padding-bottom: 0.8em;
}