.search {
  /* position: relative; */
  /* width: 20%; */
  height: 2rem;
  min-width: 80px;
  box-shadow: 0px 2px 4px rgb(150, 150, 150);
  border-radius: 5px;
  position: relative;
  display: flex;
}

.search input {
  width: 100%;
  border: none;
  border-right: none;
  padding: 0px 10px 0px 13px;
  height: 100%;
  
  border-radius: 5px;
  outline: none;
  color: #9dbfaf;
  font-size: 0.9rem;
}

.searchButton {
  width: 4rem;
  height: 85%;
  border: 0px solid #00b4cc;
  border-left: 2.5px solid #035a7073; 
  background: #ffffff;
  color: #ffffff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0.2rem;
}
