.navbar {
  width: 80%;
  height: 4rem;
  min-width: 100px;
  box-shadow: 0px 2px 4px rgb(150, 150, 150);
  border-radius: 5px;
  position: relative;
  display: flex;
  align-items: center;
}
.container-logo {
  display: flex;
  align-items: center;
  height: 100%;
}
.logo {
  margin-right: 2rem;
  width: 2.5rem;
  margin-top: 0.3rem;
  margin-left: 3rem;
}
.container-logo div:last-child {
  font-size: 0.9rem;
}
.line-nav {
  height: 45%;
  border-left: 2px solid rgb(116, 116, 116);
  margin-right: 1.2rem;
}
.container-links {
  /* background-color: aquamarine; */
  display: flex;
  margin-left: auto;
}
.link-nav {
  margin-right: 3rem;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  /* margin-left: auto; */
}
.action {
  /* background-color: bisque; */
  margin-right: 3rem;
  /* margin-left: auto; */
}
